import React from 'react';
import styles from './Footer.module.css';

interface FooterProps {
  copyrightHolder?: string;
  copyrightYear?: string;
  email?: string;
  socialLinks?: {
    platform: string;
    url: string;
    icon?: string;
  }[];
  links?: {
    label: string;
    href: string;
  }[];
  className?: string;
}

const Footer: React.FC<FooterProps> = ({
  copyrightHolder = "CROM",
  copyrightYear = "2025",
  email,
  socialLinks = [],
  links = [
    { label: '', href: '#privacy' },
    { label: '', href: '#terms' },
    { label: '', href: '#contact' },
  ],
  className
}) => {
  const currentYear = new Date().getFullYear();
  const displayYear = copyrightYear === currentYear.toString()
    ? copyrightYear
    : `${copyrightYear}-${currentYear}`;

  return (
    <footer className={`${styles.footer} ${className || ''}`}>
      <div className={styles.container}>
        {/*   */}
        <div className={styles.topSection}>
          {/* /  */}
          <div className={styles.brandSection}>
            <h3 className={styles.brandTitle}>CROM</h3>
            <p className={styles.brandDescription}>
              "Here I Stand For You"
              <br />
                
            </p>
          </div>

          {/*   */}
          <div className={styles.linkSection}>
            <h4 className={styles.linkTitle}></h4>
            <ul className={styles.linkList}>
              {links.map((link, index) => (
                <li key={index}>
                  <a
                    href={link.href}
                    className={styles.link}
                    onClick={(e) => {
                      //      
                      if (link.href.startsWith('#')) {
                        e.preventDefault();
                        console.log(`Navigate to: ${link.href}`);
                      }
                    }}
                  >
                    {link.label}
                  </a>
                </li>
              ))}
            </ul>
          </div>

          {/*   */}
          {(email || socialLinks.length > 0) && (
            <div className={styles.contactSection}>
              <h4 className={styles.contactTitle}></h4>

              {email && (
                <a
                  href={`mailto:${email}`}
                  className={styles.email}
                >
                  {email}
                </a>
              )}

              {socialLinks.length > 0 && (
                <div className={styles.socialLinks}>
                  {socialLinks.map((social, index) => (
                    <a
                      key={index}
                      href={social.url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className={styles.socialLink}
                      aria-label={social.platform}
                    >
                      {social.icon || social.platform}
                    </a>
                  ))}
                </div>
              )}
            </div>
          )}

          {/*    */}
          <div className={styles.infoSection}>
            <h4 className={styles.infoTitle}></h4>
            <p className={styles.infoText}>
                   
              <br />
                .
              <br />
              <br />
                
              <br />
               .
            </p>
          </div>
        </div>

        {/*  */}
        <div className={styles.divider} />

        {/*   -  */}
        <div className={styles.bottomSection}>
          <p className={styles.copyright}>
             {displayYear} {copyrightHolder}. All rights reserved.
          </p>

          {/*   */}
          <p className={styles.memorial}>
            1968.05.06 - 2014.10.27 | Forever in our hearts
          </p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;