/**
 * Header 
 *      
 *    (   )
 */

import React, { useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import styles from './Header.module.css';
import LoginModal from '../Auth/LoginModal';

interface HeaderProps {
  logo?: string;
  title?: string;
  className?: string;
}

const Header: React.FC<HeaderProps> = ({
  logo,
  title = "CROM",
  className
}) => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [activeDropdown, setActiveDropdown] = useState<string | null>(null);
  const dropdownTimeout = React.useRef<NodeJS.Timeout | null>(null);
  const navigate = useNavigate();
  const location = useLocation();

  const navItems = [
    {
      id: 'carousel',
      label: '',
      children: [
        { id: 'carousel', label: '' },
        { id: 'hero', label: '' }
      ]
    },
    { id: 'about', label: '' },
    { id: 'discography', label: '' },
    {
      id: 'gallery',
      label: '',
      children: [
        { id: 'gallery', label: '' },
        { id: 'street-gallery', label: ' ' }
      ]
    },
    { id: 'memorial', label: '' }
  ];

  React.useEffect(() => {
    return () => {
      if (dropdownTimeout.current) {
        clearTimeout(dropdownTimeout.current);
      }
    };
  }, []);

  //       
  React.useEffect(() => {
    const handleDesktopMenuClickOutside = (event: MouseEvent) => {
      if (activeDropdown) {
        const target = event.target as HTMLElement;
        const navList = document.querySelector(`.${styles.navList}`);
        const dropdown = document.querySelector(`.${styles.dropdown}`);

        //        
        if (navList && !navList.contains(target) &&
            (!dropdown || !dropdown.contains(target))) {
          setActiveDropdown(null);
        }
      }
    };

    if (activeDropdown) {
      //      (    )
      setTimeout(() => {
        document.addEventListener('click', handleDesktopMenuClickOutside);
        document.addEventListener('touchstart', handleDesktopMenuClickOutside);
      }, 100);
    }

    return () => {
      document.removeEventListener('click', handleDesktopMenuClickOutside);
      document.removeEventListener('touchstart', handleDesktopMenuClickOutside);
    };
  }, [activeDropdown]);

  //      
  React.useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (isMobileMenuOpen) {
        const target = event.target as HTMLElement;
        const mobileMenu = document.querySelector(`.${styles.mobileMenu}`);
        const menuButton = document.querySelector(`.${styles.mobileMenuButton}`);

        if (mobileMenu && !mobileMenu.contains(target) &&
            menuButton && !menuButton.contains(target)) {
          setIsMobileMenuOpen(false);
        }
      }
    };

    if (isMobileMenuOpen) {
      //       
      setTimeout(() => {
        document.addEventListener('mousedown', handleClickOutside);
        document.addEventListener('touchstart', handleClickOutside);
      }, 100);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('touchstart', handleClickOutside);
    };
  }, [isMobileMenuOpen]);

  const scrollToSection = (sectionId: string) => {
    if (location.pathname !== '/') {
      navigate('/');
      setTimeout(() => {
        const element = document.getElementById(sectionId);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      }, 100);
    } else {
      const element = document.getElementById(sectionId);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    }
    setIsMobileMenuOpen(false);
  };

  return (
    <header className={`${styles.header} ${className || ''}`}>
      <div className={styles.container}>
        <div className={styles.logoSection}>
          {logo && <img src={logo} alt="Logo" className={styles.logo} />}
          <h1 className={styles.title}>
            <Link to="/" onClick={(e) => {
              e.preventDefault();
              const element = document.querySelector('[class*="imageCarousel"]');
              if (element) {
                element.scrollIntoView({ behavior: 'smooth' });
              } else {
                window.scrollTo({ top: 0, behavior: 'smooth' });
              }
              navigate('/');
            }}>
              {title}
              <span className={styles.subtitle}>Memorial</span>
            </Link>
          </h1>
        </div>

        <nav className={styles.desktopNav}>
          <ul className={styles.navList}>
            {navItems.map((item) => (
              <li
                key={item.id}
                className={styles.navItem}
                onMouseEnter={() => {
                  if (dropdownTimeout.current) {
                    clearTimeout(dropdownTimeout.current);
                    dropdownTimeout.current = null;
                  }
                  //   ,    
                  if (item.children) {
                    setActiveDropdown(item.id);
                  } else {
                    //       
                    setActiveDropdown(null);
                  }
                }}
                onMouseLeave={() => {
                  if (item.children) {
                    dropdownTimeout.current = setTimeout(() => {
                      setActiveDropdown(null);
                    }, 100);
                  }
                }}
              >
                <button
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    //      (CROM   )
                    if (item.id === 'carousel' && item.label === '') {
                      const element = document.querySelector('[class*="imageCarousel"]');
                      if (element) {
                        element.scrollIntoView({ behavior: 'smooth' });
                      } else {
                        window.scrollTo({ top: 0, behavior: 'smooth' });
                      }
                      //   
                      if (item.children) {
                        setActiveDropdown(activeDropdown === item.id ? null : item.id);
                      }
                    }
                    //    gallery  
                    else if (item.id === 'gallery' && item.label === '') {
                      scrollToSection('gallery');
                      //   
                      if (item.children) {
                        setActiveDropdown(activeDropdown === item.id ? null : item.id);
                      }
                    } else {
                      scrollToSection(item.id);
                      setActiveDropdown(null);
                    }
                  }}
                  className={styles.navLink}
                >
                  {item.label}
                </button>
                {item.children && activeDropdown === item.id && (
                  <div className={styles.dropdown}>
                    {item.children.map((child) => (
                      <button
                        key={child.id}
                        onClick={() => {
                          //     
                          if (child.id === 'carousel' && child.label === '') {
                            const element = document.querySelector('[class*="imageCarousel"]');
                            if (element) {
                              element.scrollIntoView({ behavior: 'smooth' });
                            } else {
                              window.scrollTo({ top: 0, behavior: 'smooth' });
                            }
                            setActiveDropdown(null);
                          } else {
                            scrollToSection(child.id);
                          }
                        }}
                        className={styles.dropdownItem}
                      >
                        {child.label}
                      </button>
                    ))}
                  </div>
                )}
              </li>
            ))}
          </ul>
        </nav>

        <div className={styles.authSection}>
          <button className={styles.loginBtn} onClick={() => setShowLoginModal(true)}>
            
          </button>
        </div>

        <button
          className={styles.mobileMenuButton}
          onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          aria-label=" "
        >
          <span className={styles.hamburger}>
            <span></span>
            <span></span>
            <span></span>
          </span>
        </button>

        {isMobileMenuOpen && (
          <nav className={styles.mobileNav}>
            <ul className={styles.mobileNavList}>
              {navItems.map((item) => (
                <li key={item.id}>
                  <button
                    onClick={() => {
                      //      (CROM   )
                      if (item.id === 'carousel' && item.label === '') {
                        const element = document.querySelector('[class*="imageCarousel"]');
                        if (element) {
                          element.scrollIntoView({ behavior: 'smooth' });
                        } else {
                          window.scrollTo({ top: 0, behavior: 'smooth' });
                        }
                        setIsMobileMenuOpen(false);
                      } else {
                        scrollToSection(item.id);
                      }
                    }}
                    className={styles.mobileNavLink}
                  >
                    {item.label}
                  </button>
                  {/*     */}
                  {item.children && (
                    <ul className={styles.mobileSubNav}>
                      {item.children.map((child) => (
                        <li key={child.id}>
                          <button
                            onClick={() => {
                              scrollToSection(child.id);
                            }}
                            className={styles.mobileSubNavLink}
                          >
                            {child.label}
                          </button>
                        </li>
                      ))}
                    </ul>
                  )}
                </li>
              ))}
            </ul>
          </nav>
        )}
      </div>

      {showLoginModal && (
        <LoginModal
          onClose={() => setShowLoginModal(false)}
          onSuccess={() => setShowLoginModal(false)}
        />
      )}
    </header>
  );
};

export default Header;