/**
 * ScrollNav 
 *    -  /
 *   , 2  ,    
 */

import React, { useState, useEffect, useRef } from 'react';
import styles from './ScrollNav.module.css';

const ScrollNav: React.FC = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [isHovered, setIsHovered] = useState(false);
  const hideTimeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null);
  const scrollTimeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null);

  useEffect(() => {
    //   
    const handleScroll = () => {
      //   
      setIsVisible(true);

      //   
      if (hideTimeoutRef.current) {
        clearTimeout(hideTimeoutRef.current);
      }
      if (scrollTimeoutRef.current) {
        clearTimeout(scrollTimeoutRef.current);
      }

      //   2   (   )
      scrollTimeoutRef.current = setTimeout(() => {
        if (!isHovered) {
          setIsVisible(false);
        }
      }, 2000);
    };

    //     -   
    const handleMouseMove = (e: MouseEvent) => {
      const threshold = 150; //  150px 
      const isNearRight = window.innerWidth - e.clientX < threshold;

      if (isNearRight) {
        setIsVisible(true);
        //     2  
        if (hideTimeoutRef.current) {
          clearTimeout(hideTimeoutRef.current);
        }
      } else if (!isHovered) {
        //       2  
        if (!hideTimeoutRef.current) {
          hideTimeoutRef.current = setTimeout(() => {
            if (!isHovered) {
              setIsVisible(false);
            }
          }, 2000);
        }
      }
    };

    window.addEventListener('scroll', handleScroll);
    window.addEventListener('mousemove', handleMouseMove);

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('mousemove', handleMouseMove);
      if (hideTimeoutRef.current) {
        clearTimeout(hideTimeoutRef.current);
      }
      if (scrollTimeoutRef.current) {
        clearTimeout(scrollTimeoutRef.current);
      }
    };
  }, [isHovered]);

  //   
  const handleMouseEnter = () => {
    setIsHovered(true);
    setIsVisible(true);
    if (hideTimeoutRef.current) {
      clearTimeout(hideTimeoutRef.current);
    }
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
    //   2  
    hideTimeoutRef.current = setTimeout(() => {
      setIsVisible(false);
    }, 2000);
  };

  //  
  const scrollUp = () => {
    const currentPosition = window.scrollY;
    if (currentPosition <= 100) {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else {
      window.scrollBy({ top: -window.innerHeight * 0.8, behavior: 'smooth' });
    }
  };

  //  
  const scrollDown = () => {
    window.scrollBy({ top: window.innerHeight * 0.8, behavior: 'smooth' });
  };

  return (
    <nav
      className={`${styles.scrollNav} ${isVisible ? styles.visible : ''}`}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <button
        className={styles.navButton}
        onClick={scrollUp}
        aria-label=" "
      >
        <svg width="16" height="16" viewBox="0 0 16 16" fill="none">
          <path d="M8 4L12 9H4L8 4Z" fill="currentColor"/>
        </svg>
      </button>
      <div className={styles.divider} />
      <button
        className={styles.navButton}
        onClick={scrollDown}
        aria-label=" "
      >
        <svg width="16" height="16" viewBox="0 0 16 16" fill="none">
          <path d="M8 12L4 7H12L8 12Z" fill="currentColor"/>
        </svg>
      </button>
    </nav>
  );
};

export default ScrollNav;