/**
 *   
 *      
 */

import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import {
  getAllMessages,
  approveMessage,
  rejectMessage,
  deleteMessage,
  type MemorialMessage
} from '../../services/memorialService';
import styles from './AdminPanel.module.css';
import type { DocumentSnapshot } from 'firebase/firestore';

const ADMIN_EMAIL = 'admin@crom-memorial.com'; //     

const AdminPanel: React.FC = () => {
  const { user, signInWithGoogle, logout } = useAuth();
  const [isAdmin, setIsAdmin] = useState(false);
  const [loading, setLoading] = useState(true);
  const [messages, setMessages] = useState<MemorialMessage[]>([]);
  const [filteredMessages, setFilteredMessages] = useState<MemorialMessage[]>([]);
  const [currentView, setCurrentView] = useState<'all' | 'pending' | 'approved' | 'rejected'>('pending');
  const [expandedMessages, setExpandedMessages] = useState<Set<string>>(new Set());
  const [lastDoc, setLastDoc] = useState<DocumentSnapshot | null>(null);
  const [hasMore, setHasMore] = useState(true);

  //       
  void lastDoc;
  void setLastDoc;
  void hasMore;
  void setHasMore;
  const [currentPage, setCurrentPage] = useState(1);
  const messagesPerPage = 10;

  // 
  const [stats, setStats] = useState({
    total: 0,
    pending: 0,
    approved: 0,
    rejected: 0,
    reported: 0
  });

  //  
  useEffect(() => {
    if (user) {
      //  Firebase Admin SDK Custom Claims  
      //    
      setIsAdmin(user.email === ADMIN_EMAIL || user.email === 'tlawlgns59@gmail.com');
    } else {
      setIsAdmin(false);
    }
    setLoading(false);
  }, [user]);

  //  
  const loadMessages = async () => {
    if (!isAdmin) return;

    try {
      setLoading(true);
      const { messages: allMessages } = await getAllMessages(1000); //   
      setMessages(allMessages);

      //  
      const stats = {
        total: allMessages.length,
        pending: allMessages.filter(m => !m.isApproved && !m.isRejected).length,
        approved: allMessages.filter(m => m.isApproved).length,
        rejected: allMessages.filter(m => m.isRejected).length,
        reported: allMessages.filter(m => (m.reportCount || 0) > 0).length
      };
      setStats(stats);
    } catch (error) {
      console.error('  :', error);
    } finally {
      setLoading(false);
    }
  };

  //  
  useEffect(() => {
    let filtered = messages;

    switch (currentView) {
      case 'pending':
        filtered = messages.filter(m => !m.isApproved && !m.isRejected);
        break;
      case 'approved':
        filtered = messages.filter(m => m.isApproved);
        break;
      case 'rejected':
        filtered = messages.filter(m => m.isRejected);
        break;
    }

    setFilteredMessages(filtered);
    setCurrentPage(1);
  }, [messages, currentView]);

  //     
  useEffect(() => {
    if (isAdmin) {
      loadMessages();
    }
  }, [isAdmin]);

  //  
  const handleApprove = async (messageId: string) => {
    if (!messageId) return;

    try {
      await approveMessage(messageId);
      await loadMessages(); //  
      alert(' .');
    } catch (error) {
      console.error(' :', error);
      alert('  .');
    }
  };

  //  
  const handleReject = async (messageId: string) => {
    if (!messageId) return;

    const reason = prompt('  :');
    if (!reason) return;

    try {
      await rejectMessage(messageId, reason);
      await loadMessages();
      alert(' .');
    } catch (error) {
      console.error(' :', error);
      alert('  .');
    }
  };

  //  
  const handleDelete = async (messageId: string) => {
    if (!messageId) return;

    if (!confirm('   ?')) return;

    try {
      await deleteMessage(messageId);
      await loadMessages();
      alert(' .');
    } catch (error) {
      console.error(' :', error);
      alert('  .');
    }
  };

  //  /
  const toggleExpand = (messageId: string) => {
    const newExpanded = new Set(expandedMessages);
    if (newExpanded.has(messageId)) {
      newExpanded.delete(messageId);
    } else {
      newExpanded.add(messageId);
    }
    setExpandedMessages(newExpanded);
  };

  // 
  const totalPages = Math.ceil(filteredMessages.length / messagesPerPage);
  const startIndex = (currentPage - 1) * messagesPerPage;
  const endIndex = startIndex + messagesPerPage;
  const currentMessages = filteredMessages.slice(startIndex, endIndex);

  //  
  if (!user || !isAdmin) {
    return (
      <section className={styles.adminPanel}>
        <div className={styles.container}>
          <div className={styles.header}>
            <h1 className={styles.title}> </h1>
            <p className={styles.subtitle}>   </p>
          </div>

          {loading ? (
            <div className={styles.loading}>...</div>
          ) : (
            <div className={styles.loginForm}>
              <h2 className={styles.loginTitle}> </h2>
              {user && !isAdmin && (
                <p className={styles.error}>  .</p>
              )}
              <button
                className={styles.loginButton}
                onClick={signInWithGoogle}
              >
                Google 
              </button>
            </div>
          )}
        </div>
      </section>
    );
  }

  //  
  return (
    <section className={styles.adminPanel}>
      <div className={styles.container}>
        <div className={styles.header}>
          <h1 className={styles.title}> </h1>
          <p className={styles.subtitle}>   </p>
        </div>

        <div className={styles.dashboard}>
          {/*  */}
          <aside className={styles.sidebar}>
            <h3 className={styles.sidebarTitle}></h3>
            <ul className={styles.sidebarMenu}>
              <li className={styles.menuItem}>
                <button
                  className={`${styles.menuButton} ${currentView === 'pending' ? styles.active : ''}`}
                  onClick={() => setCurrentView('pending')}
                >
                   ({stats.pending})
                </button>
              </li>
              <li className={styles.menuItem}>
                <button
                  className={`${styles.menuButton} ${currentView === 'approved' ? styles.active : ''}`}
                  onClick={() => setCurrentView('approved')}
                >
                   ({stats.approved})
                </button>
              </li>
              <li className={styles.menuItem}>
                <button
                  className={`${styles.menuButton} ${currentView === 'rejected' ? styles.active : ''}`}
                  onClick={() => setCurrentView('rejected')}
                >
                   ({stats.rejected})
                </button>
              </li>
              <li className={styles.menuItem}>
                <button
                  className={`${styles.menuButton} ${currentView === 'all' ? styles.active : ''}`}
                  onClick={() => setCurrentView('all')}
                >
                   ({stats.total})
                </button>
              </li>
            </ul>
            <button className={styles.logoutButton} onClick={logout}>
              
            </button>
          </aside>

          {/*   */}
          <main className={styles.mainContent}>
            <div className={styles.contentHeader}>
              <h2 className={styles.contentTitle}>
                {currentView === 'pending' && '  '}
                {currentView === 'approved' && ' '}
                {currentView === 'rejected' && ' '}
                {currentView === 'all' && ' '}
              </h2>
              <button onClick={loadMessages}></button>
            </div>

            {/*  */}
            <div className={styles.stats}>
              <div className={styles.statCard}>
                <p className={styles.statLabel}> </p>
                <p className={styles.statValue}>{stats.total}</p>
              </div>
              <div className={styles.statCard}>
                <p className={styles.statLabel}></p>
                <p className={styles.statValue}>{stats.pending}</p>
              </div>
              <div className={styles.statCard}>
                <p className={styles.statLabel}></p>
                <p className={styles.statValue}>{stats.approved}</p>
              </div>
              <div className={styles.statCard}>
                <p className={styles.statLabel}></p>
                <p className={styles.statValue}>{stats.reported}</p>
              </div>
            </div>

            {/*   */}
            {loading ? (
              <div className={styles.loading}>...</div>
            ) : (
              <>
                <table className={styles.messageTable}>
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentMessages.map((msg) => (
                      <tr key={msg.id}>
                        <td>{msg.author}</td>
                        <td>
                          <div className={expandedMessages.has(msg.id!) ? styles.messageFull : styles.messagePreview}>
                            {msg.message}
                          </div>
                          {msg.message.length > 100 && (
                            <button
                              className={styles.expandButton}
                              onClick={() => toggleExpand(msg.id!)}
                            >
                              {expandedMessages.has(msg.id!) ? '' : ''}
                            </button>
                          )}
                        </td>
                        <td>{msg.createdAt.toLocaleDateString('ko-KR')}</td>
                        <td>
                          <span className={`${styles.statusBadge} ${
                            msg.isApproved ? styles.approved :
                            msg.isRejected ? styles.rejected :
                            styles.pending
                          }`}>
                            {msg.isApproved ? '' : msg.isRejected ? '' : ''}
                          </span>
                        </td>
                        <td>{msg.reportCount || 0}</td>
                        <td>
                          <div className={styles.actionButtons}>
                            {!msg.isApproved && !msg.isRejected && (
                              <>
                                <button
                                  className={styles.approveButton}
                                  onClick={() => handleApprove(msg.id!)}
                                >
                                  
                                </button>
                                <button
                                  className={styles.rejectButton}
                                  onClick={() => handleReject(msg.id!)}
                                >
                                  
                                </button>
                              </>
                            )}
                            <button
                              className={styles.deleteButton}
                              onClick={() => handleDelete(msg.id!)}
                            >
                              
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>

                {/*  */}
                {totalPages > 1 && (
                  <div className={styles.pagination}>
                    <button
                      className={styles.pageButton}
                      onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                      disabled={currentPage === 1}
                    >
                      
                    </button>
                    <span className={styles.pageInfo}>
                      {currentPage} / {totalPages}
                    </span>
                    <button
                      className={styles.pageButton}
                      onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                      disabled={currentPage === totalPages}
                    >
                      
                    </button>
                  </div>
                )}
              </>
            )}
          </main>
        </div>
      </div>
    </section>
  );
};

export default AdminPanel;