import React, { useState } from 'react';
import { auth, googleProvider } from '../../config/firebase';
import { signInWithPopup, signInAnonymously, signInWithEmailAndPassword, createUserWithEmailAndPassword } from 'firebase/auth';
import './LoginModal.css';

interface LoginModalProps {
  onClose: () => void;
  onSuccess?: () => void;
}

const LoginModal: React.FC<LoginModalProps> = ({ onClose, onSuccess }) => {
  const [isSignUp, setIsSignUp] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleGoogleLogin = async () => {
    setLoading(true);
    setError('');
    try {
      await signInWithPopup(auth, googleProvider);
      onSuccess?.();
      onClose();
    } catch (err: any) {
      setError('Google  : ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleAnonymousLogin = async () => {
    setLoading(true);
    setError('');
    try {
      await signInAnonymously(auth);
      onSuccess?.();
      onClose();
    } catch (err: any) {
      setError('  : ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleEmailAuth = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      if (isSignUp) {
        await createUserWithEmailAndPassword(auth, email, password);
      } else {
        await signInWithEmailAndPassword(auth, email, password);
      }
      onSuccess?.();
      onClose();
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-modal-overlay" onClick={onClose}>
      <div className="login-modal" onClick={(e) => e.stopPropagation()}>
        <button className="close-button" onClick={onClose}></button>

        <h2>{isSignUp ? '' : ''}</h2>

        <div className="social-login-section">
          <button
            className="google-login-btn"
            onClick={handleGoogleLogin}
            disabled={loading}
          >
            <img src="https://www.google.com/favicon.ico" alt="Google" />
            Google {isSignUp ? '' : ''}
          </button>

          <button
            className="anonymous-login-btn"
            onClick={handleAnonymousLogin}
            disabled={loading}
          >
               
          </button>
        </div>

        <div className="divider">
          <span></span>
        </div>

        <form onSubmit={handleEmailAuth} className="email-login-form">
          <input
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />

          {error && <p className="error-message">{error}</p>}

          <button type="submit" disabled={loading}>
            {loading ? '...' : (isSignUp ? '' : '')}
          </button>
        </form>

        <p className="toggle-auth">
          {isSignUp ? '  ?' : ' ?'}
          <button onClick={() => setIsSignUp(!isSignUp)}>
            {isSignUp ? '' : ''}
          </button>
        </p>
      </div>
    </div>
  );
};

export default LoginModal;