/**
 * ImageCarousel 
 *   -  ,  
 */

import React, { useState, useEffect } from 'react';
import styles from './ImageCarousel.module.css';

interface CarouselImage {
  id: string;
  url: string;
  alt: string;
  caption?: string;
  year?: string;
}

interface ImageCarouselProps {
  images: CarouselImage[];
  autoPlay?: boolean;
  interval?: number;
}

const ImageCarousel: React.FC<ImageCarouselProps> = ({
  images,
  autoPlay = false,
  interval = 5000
}) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isHovered, setIsHovered] = useState(false);
  const [showCaption, setShowCaption] = useState(false);
  const [hideTimer, setHideTimer] = useState<number | null>(null);

  //  
  useEffect(() => {
    if (!autoPlay || isHovered || images.length <= 1) return;

    const timer = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % images.length);
    }, interval);

    return () => clearInterval(timer);
  }, [autoPlay, isHovered, images.length, interval, currentIndex]);

  //  
  const goToPrevious = () => {
    setCurrentIndex((prev) => (prev - 1 + images.length) % images.length);
  };

  //  
  const goToNext = () => {
    setCurrentIndex((prev) => (prev + 1) % images.length);
  };

  //   
  const goToSlide = (index: number) => {
    setCurrentIndex(index);
  };

  //  
  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'ArrowLeft') {
      goToPrevious();
    } else if (e.key === 'ArrowRight') {
      goToNext();
    }
  };

  //   
  const handleCaptionHover = () => {
    //   
    if (hideTimer) {
      clearTimeout(hideTimer);
      setHideTimer(null);
    }
    setShowCaption(true);
  };

  const handleCaptionLeave = () => {
    // 2   
    const timer = setTimeout(() => {
      setShowCaption(false);
    }, 2000);
    setHideTimer(timer);
  };

  //    
  useEffect(() => {
    return () => {
      if (hideTimer) {
        clearTimeout(hideTimer);
      }
    };
  }, [hideTimer]);

  //   
  const [touchStart, setTouchStart] = useState(0);
  const [touchEnd, setTouchEnd] = useState(0);

  const handleTouchStart = (e: React.TouchEvent) => {
    setTouchStart(e.targetTouches[0].clientX);
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const handleTouchEnd = () => {
    if (!touchStart || !touchEnd) return;

    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > 50;
    const isRightSwipe = distance < -50;

    if (isLeftSwipe) {
      goToNext();
    } else if (isRightSwipe) {
      goToPrevious();
    }
  };

  if (!images || images.length === 0) return null;

  //   
  const handleClickArea = (e: React.MouseEvent) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const width = rect.width;

    //  1/3   
    if (x < width / 3) {
      goToPrevious();
    }
    //  1/3   
    else if (x > (width * 2) / 3) {
      goToNext();
    }
    //    
  };

  //  
  const handleDragStart = (e: React.DragEvent) => {
    e.preventDefault();
  };

  return (
    <div
      className={styles.carousel}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onKeyDown={handleKeyDown}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
      tabIndex={0}
      role="region"
      aria-label=" "
      aria-roledescription="carousel"
    >
      {/*   */}
      <div
        className={styles.imageContainer}
        onClick={handleClickArea}
      >
        {images.map((image, index) => (
          <div
            key={image.id}
            className={`${styles.slide} ${index === currentIndex ? styles.active : ''}`}
            aria-hidden={index !== currentIndex}
          >
            <picture>
              <source
                srcSet={image.url.replace(/\.(jpg|jpeg|png)$/i, '.webp')}
                type="image/webp"
              />
              <img
                src={image.url}
                alt={image.alt}
                className={styles.image}
                onDragStart={handleDragStart}
                draggable={false}
              />
            </picture>
            {image.caption && (
              <div
                className={`${styles.caption} ${showCaption ? styles.show : styles.hide}`}
                onMouseEnter={handleCaptionHover}
                onMouseLeave={handleCaptionLeave}
              >
                <p className={styles.captionText}>{image.caption}</p>
                {image.year && <span className={styles.year}>{image.year}</span>}
              </div>
            )}
          </div>
        ))}

        {/*    */}
        {images.length > 1 && (
          <>
            <button
              className={`${styles.arrow} ${styles.arrowLeft}`}
              onClick={goToPrevious}
              aria-label=" "
            >
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                <path d="M15 18L9 12L15 6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
            <button
              className={`${styles.arrow} ${styles.arrowRight}`}
              onClick={goToNext}
              aria-label=" "
            >
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                <path d="M9 6L15 12L9 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
          </>
        )}
      </div>

      {/*   */}
      {images.length > 1 && (
        <div className={styles.indicators}>
          {images.map((_, index) => (
            <button
              key={index}
              className={`${styles.dot} ${index === currentIndex ? styles.activeDot : ''}`}
              onClick={() => goToSlide(index)}
              aria-label={`${index + 1}  `}
              aria-current={index === currentIndex}
            />
          ))}
          <span className={styles.counter}>
            {currentIndex + 1} / {images.length}
          </span>
        </div>
      )}
    </div>
  );
};

export default ImageCarousel;