/**
 * Hero 
 *    -     
 *   
 */

import React, { useEffect, useState } from 'react';
import styles from './Hero.module.css';

interface HeroProps {
  //   props
  backgroundImage?: string;
  title?: string;
  subtitle?: string;
  description?: string;
  showScrollIndicator?: boolean;
  parallax?: boolean;
  overlay?: boolean;
  className?: string;
  onScrollClick?: () => void;
}

const Hero: React.FC<HeroProps> = ({
  backgroundImage = '/images/hero-default.jpg',
  title = " ",
  subtitle = "",
  description = "     ",
  showScrollIndicator = true,
  parallax = true,
  overlay = true,
  className,
  onScrollClick
}) => {
  //      
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    if (!parallax) return;

    const handleScroll = () => {
      setScrollY(window.scrollY);
    };

    //    
    window.addEventListener('scroll', handleScroll);

    //  
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [parallax]);

  //   
  const handleScrollDown = () => {
    if (onScrollClick) {
      onScrollClick();
    } else {
      //  :    
      window.scrollTo({
        top: window.innerHeight,
        behavior: 'smooth'
      });
    }
  };

  //   
  const parallaxStyle = parallax ? {
    transform: `translateY(${scrollY * 0.5}px)`
  } : {};

  return (
    <section className={`${styles.hero} ${className || ''}`}>
      {/*    */}
      <div
        className={styles.backgroundLayer}
        style={{
          backgroundImage: `url(${backgroundImage})`,
          ...parallaxStyle
        }}
      />

      {/*   () */}
      {overlay && <div className={styles.overlay} />}

      {/*   */}
      <div className={styles.content}>
        <div className={styles.textContainer}>
          {/*   */}
          <h1 className={`${styles.title} ${!subtitle ? styles.noSubtitle : ''}`} data-aos="fade-up">
            {title}
          </h1>

          {/*  */}
          {subtitle && (
            <h2 className={styles.subtitle} data-aos="fade-up" data-aos-delay="100">
              {subtitle}
            </h2>
          )}

          {/*   */}
          {description && (
            <p className={styles.description} data-aos="fade-up" data-aos-delay="200">
              {description}
            </p>
          )}

          {/* CTA   ( ) */}
          <div className={styles.ctaGroup} data-aos="fade-up" data-aos-delay="300">
            <button
              className={styles.primaryBtn}
              onClick={() => {
                const memorialSection = document.getElementById('memorial');
                if (memorialSection) {
                  memorialSection.scrollIntoView({ behavior: 'smooth' });
                }
              }}
            >
               
            </button>
            <button
              className={styles.secondaryBtn}
              onClick={() => {
                const gallerySection = document.getElementById('gallery');
                if (gallerySection) {
                  gallerySection.scrollIntoView({ behavior: 'smooth' });
                }
              }}
            >
              
            </button>
          </div>
        </div>

        {/*   */}
        {showScrollIndicator && (
          <button
            className={styles.scrollIndicator}
            onClick={handleScrollDown}
            aria-label=" "
          >
            <span className={styles.scrollIcon}>
              <span className={styles.scrollDot} />
            </span>
            <span className={styles.scrollText}>Scroll</span>
          </button>
        )}
      </div>
    </section>
  );
};

export default Hero;