/**
 *   
 * Firestore   CRUD 
 */

import {
  collection,
  addDoc,
  getDocs,
  doc,
  updateDoc,
  deleteDoc,
  query,
  orderBy,
  limit,
  where,
  Timestamp,
  startAfter,
  DocumentSnapshot,
  serverTimestamp
} from 'firebase/firestore';
import { db } from '../config/firebase';

export interface MemorialMessage {
  id?: string;
  author: string;
  message: string;
  password?: string; //  (   )
  createdAt: Date;
  userId?: string;
  userEmail?: string;
  isAnonymous: boolean;
  isApproved?: boolean; // Firebase  
  approved?: boolean; // Legacy    
  isRejected?: boolean;
  rejectionReason?: string;
  likes: number;
  reportCount?: number;
  reports?: number; // Legacy    
  isLegacy?: boolean; //   
}

const COLLECTION_NAME = 'memorial_messages';

