/**
 *   (   )
 */
export const deleteMessage = async (
  messageId: string,
  isLegacy: boolean = false
): Promise<void> => {
  try {
    if (isLegacy) {
      //     isApproved false 
      const docRef = doc(db, COLLECTION_NAME, messageId);
      await updateDoc(docRef, {
        isApproved: false,
        deletedAt: Timestamp.now(),
        deletedBy: 'admin'
      });
    } else {
      //    
      await deleteDoc(doc(db, COLLECTION_NAME, messageId));
    }
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

/**
 *  
 */
export const updateMessage = async (
  messageId: string,
  updates: Partial<MemorialMessage>
): Promise<void> => {
  try {
    const docRef = doc(db, COLLECTION_NAME, messageId);
    await updateDoc(docRef, {
      ...updates,
      updatedAt: Timestamp.now()
    });
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

/**
 *  
 */
export const createMessage = async (
  message: Omit<MemorialMessage, 'id' | 'createdAt' | 'likes' | 'isApproved'> & { password?: string }
): Promise<string> => {
  try {
    const docRef = await addDoc(collection(db, COLLECTION_NAME), {
      ...message,
      createdAt: Timestamp.now(),
      likes: 0,
      isApproved: false, //   
      reportCount: 0
    });
    return docRef.id;
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

/**
 *    
 */
export const getApprovedMessages = async (
  limitCount = 20,
  lastDoc?: DocumentSnapshot
): Promise<{ messages: MemorialMessage[]; lastDoc: DocumentSnapshot | null }> => {
  console.log(' getApprovedMessages :', { limitCount, hasLastDoc: !!lastDoc });

  try {
    let q = query(
      collection(db, COLLECTION_NAME),
      where('isApproved', '==', true),
      orderBy('createdAt', 'desc'),
      limit(limitCount)
    );

    if (lastDoc) {
      q = query(
        collection(db, COLLECTION_NAME),
        where('isApproved', '==', true),
        orderBy('createdAt', 'desc'),
        startAfter(lastDoc),
        limit(limitCount)
      );
    }

    console.log(' Firebase   ...');
    const snapshot = await getDocs(q);

    console.log(' Firebase  :', {
      docsCount: snapshot.docs.length,
      isEmpty: snapshot.empty
    });

    const messages = snapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data(),
      createdAt: doc.data().createdAt?.toDate() || new Date()
    })) as MemorialMessage[];

    const lastDocument = snapshot.docs[snapshot.docs.length - 1] || null;

    console.log(' Firebase   :', {
      messagesCount: messages.length,
      hasLastDoc: !!lastDocument
    });

    return { messages, lastDoc: lastDocument };
  } catch (error: any) {
    console.error('   :', {
      error: error.message,
      code: error.code,
      details: error
    });

    // Firebase    
    if (error.code === 'permission-denied') {
      console.warn(' Firebase  . Legacy  .');
    } else if (error.code === 'unavailable') {
      console.warn(' Firebase    . Legacy  .');
    } else if (error.code === 'failed-precondition' || error.code === 'unimplemented') {
      console.warn(' Firestore    .');
    }

    return { messages: [], lastDoc: null };
  }
};

/**
 *     ()
 */
export const getPendingMessages = async (): Promise<MemorialMessage[]> => {
  try {
    const q = query(
      collection(db, COLLECTION_NAME),
      where('isApproved', '==', false),
      orderBy('createdAt', 'desc')
    );

    const snapshot = await getDocs(q);
    return snapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data(),
      createdAt: doc.data().createdAt?.toDate() || new Date()
    })) as MemorialMessage[];
  } catch (error) {
    console.error('   :', error);
    return [];
  }
};

/**
 *   ()
 */
export const approveMessage = async (messageId: string): Promise<void> => {
  try {
    const messageRef = doc(db, COLLECTION_NAME, messageId);
    await updateDoc(messageRef, {
      isApproved: true,
      approvedAt: Timestamp.now()
    });
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

/**
 *  
 */
export const likeMessage = async (messageId: string): Promise<void> => {
  try {
    const messageRef = doc(db, COLLECTION_NAME, messageId);
    const snapshot = await getDocs(query(collection(db, COLLECTION_NAME), where('__name__', '==', messageId)));

    if (!snapshot.empty) {
      const currentLikes = snapshot.docs[0].data().likes || 0;
      await updateDoc(messageRef, {
        likes: currentLikes + 1
      });
    }
  } catch (error) {
    console.error(' :', error);
    throw error;
  }
};

/**
 *  
 */
export const reportMessage = async (messageId: string, reason?: string): Promise<void> => {
  try {
    const messageRef = doc(db, COLLECTION_NAME, messageId);
    const snapshot = await getDocs(query(collection(db, COLLECTION_NAME), where('__name__', '==', messageId)));

    if (!snapshot.empty) {
      const currentReports = snapshot.docs[0].data().reportCount || 0;
      await updateDoc(messageRef, {
        reportCount: currentReports + 1,
        lastReported: serverTimestamp()
      });

      //      (   )
      if (reason) {
        const reportData = {
          messageId,
          reason,
          reportedAt: serverTimestamp(),
          status: 'pending' // pending, reviewed, resolved
        };

        await addDoc(collection(db, 'reports'), reportData);
      }

      //  5    
      if (currentReports + 1 >= 5) {
        await updateDoc(messageRef, {
          isApproved: false
        });
      }
    }
  } catch (error) {
    console.error(' :', error);
    throw error;
  }
};

/**
 *   ()
 */
export const rejectMessage = async (messageId: string, reason: string): Promise<void> => {
  try {
    const messageRef = doc(db, COLLECTION_NAME, messageId);
    await updateDoc(messageRef, {
      isApproved: false,
      isRejected: true,
      rejectionReason: reason,
      rejectedAt: Timestamp.now()
    });
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

/**
 *    ()
 */
export const getAllMessages = async (
  limitCount = 100,
  lastDoc?: DocumentSnapshot
): Promise<{ messages: MemorialMessage[]; lastDoc: DocumentSnapshot | null }> => {
  try {
    let q = query(
      collection(db, COLLECTION_NAME),
      orderBy('createdAt', 'desc'),
      limit(limitCount)
    );

    if (lastDoc) {
      q = query(
        collection(db, COLLECTION_NAME),
        orderBy('createdAt', 'desc'),
        startAfter(lastDoc),
        limit(limitCount)
      );
    }

    const snapshot = await getDocs(q);
    const messages = snapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data(),
      createdAt: doc.data().createdAt?.toDate() || new Date()
    })) as MemorialMessage[];

    const lastDocument = snapshot.docs[snapshot.docs.length - 1] || null;

    return { messages, lastDoc: lastDocument };
  } catch (error) {
    console.error('  :', error);
    return { messages: [], lastDoc: null };
  }
};