/**
 * MediaGallery 
 * , ,     
 *     
 */

import React, { useState, useCallback, useEffect } from 'react';
import styles from './MediaGallery.module.css';
import type { MediaItem } from '../../types';

interface MediaGalleryProps {
  photos?: MediaItem[];
  music?: MediaItem[];
  videos?: MediaItem[];
  initialTab?: 'photos' | 'music' | 'videos';
  columns?: number;
  className?: string;
  onItemClick?: (item: MediaItem) => void;
}

const MediaGallery: React.FC<MediaGalleryProps> = ({
  photos = [],
  music = [],
  videos = [],
  initialTab = 'photos',
  columns = 3,
  className,
  onItemClick
}) => {
  //    
  const [activeTab, setActiveTab] = useState<'photos' | 'music' | 'videos'>(initialTab);

  //    ()
  const [selectedItem, setSelectedItem] = useState<MediaItem | null>(null);
  const [selectedIndex, setSelectedIndex] = useState<number>(-1);

  //  
  const [currentPage, setCurrentPage] = useState(0);
  const [itemsPerPage, setItemsPerPage] = useState(3);

  //    
  const [touchStart, setTouchStart] = useState<number | null>(null);
  const [touchEnd, setTouchEnd] = useState<number | null>(null);

  //    
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 768) {
        setItemsPerPage(1);
      } else {
        setItemsPerPage(3);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  //     
  const getCurrentItems = useCallback(() => {
    switch (activeTab) {
      case 'photos':
        return photos;
      case 'music':
        return music;
      case 'videos':
        return videos;
      default:
        return [];
    }
  }, [activeTab, photos, music, videos]);

  //    
  const handleItemClick = (item: MediaItem, index: number) => {
    if (onItemClick) {
      onItemClick(item);
    } else {
      //  :  
      setSelectedItem(item);
      setSelectedIndex(index);
    }
  };

  //  
  const closeLightbox = () => {
    setSelectedItem(null);
    setSelectedIndex(-1);
  };

  // /  (  )
  const navigatePrevious = useCallback(() => {
    const items = getCurrentItems();
    if (selectedIndex > 0) {
      setSelectedIndex(selectedIndex - 1);
      setSelectedItem(items[selectedIndex - 1]);
    } else {
      //     
      setSelectedIndex(items.length - 1);
      setSelectedItem(items[items.length - 1]);
    }
  }, [selectedIndex, getCurrentItems]);

  const navigateNext = useCallback(() => {
    const items = getCurrentItems();
    if (selectedIndex < items.length - 1) {
      setSelectedIndex(selectedIndex + 1);
      setSelectedItem(items[selectedIndex + 1]);
    } else {
      //     
      setSelectedIndex(0);
      setSelectedItem(items[0]);
    }
  }, [selectedIndex, getCurrentItems]);

  // ESC   
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && selectedItem) {
        closeLightbox();
      } else if (e.key === 'ArrowLeft' && selectedItem) {
        navigatePrevious();
      } else if (e.key === 'ArrowRight' && selectedItem) {
        navigateNext();
      }
    };

    if (selectedItem) {
      document.addEventListener('keydown', handleKeyDown);
      return () => document.removeEventListener('keydown', handleKeyDown);
    }
  }, [selectedItem, closeLightbox, navigatePrevious, navigateNext]);

  //   
  const minSwipeDistance = 50; //   

  const onTouchStart = (e: React.TouchEvent) => {
    setTouchEnd(null);
    setTouchStart(e.targetTouches[0].clientX);
  };

  const onTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;

    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe) {
      navigateNext();
    } else if (isRightSwipe) {
      navigatePrevious();
    }
  };

  //   
  const renderMediaItem = (item: MediaItem, index: number) => {
    switch (item.type) {
      case 'photo':
        return (
          <div
            key={item.id}
            className={styles.photoItem}
            onClick={() => handleItemClick(item, index)}
          >
            <picture>
              <source
                srcSet={
                  item.thumbnailUrl
                    ? item.thumbnailUrl.replace(/\.(jpg|jpeg|png)$/i, '.webp')
                    : `/images/thumbnails/${item.url.split('/').pop()?.replace(/\.(jpg|jpeg|png)$/i, '.webp')}`
                }
                type="image/webp"
              />
              <img
                src={
                  item.thumbnailUrl ||
                  `/images/thumbnails/${item.url.split('/').pop()?.replace(/\.(jpg|jpeg|png)$/i, '.jpg')}`
                }
                alt={item.title}
                loading="lazy"
                className={styles.thumbnail}
              />
            </picture>
            <div className={styles.itemOverlay}>
              <h4 className={styles.itemTitle}>{item.title}</h4>
              {item.date && <span className={styles.itemDate}>{item.date}</span>}
            </div>
          </div>
        );

      case 'music':
        return (
          <div
            key={item.id}
            className={styles.musicItem}
            onClick={() => handleItemClick(item, index)}
          >
            <div className={styles.musicThumbnail}>
              {item.thumbnailUrl ? (
                <img src={item.thumbnailUrl} alt={item.title} />
              ) : (
                <div className={styles.musicIcon}></div>
              )}
            </div>
            <div className={styles.musicInfo}>
              <h4 className={styles.musicTitle}>{item.title}</h4>
              {item.description && (
                <p className={styles.musicDescription}>{item.description}</p>
              )}
            </div>
            <button className={styles.playButton} aria-label="">
              
            </button>
          </div>
        );

      case 'video':
        return (
          <div
            key={item.id}
            className={styles.videoItem}
            onClick={() => handleItemClick(item, index)}
          >
            <div className={styles.videoThumbnail}>
              <img
                src={item.thumbnailUrl || '/images/video-placeholder.jpg'}
                alt={item.title}
                loading="lazy"
              />
              <div className={styles.playOverlay}>
                <span className={styles.playIcon}></span>
              </div>
            </div>
            <h4 className={styles.videoTitle}>{item.title}</h4>
            {item.description && (
              <p className={styles.videoDescription}>{item.description}</p>
            )}
          </div>
        );

      default:
        return null;
    }
  };

  const currentItems = getCurrentItems();

  //  
  const totalPages = Math.ceil(currentItems.length / itemsPerPage);
  const paginatedItems = currentItems.slice(
    currentPage * itemsPerPage,
    (currentPage + 1) * itemsPerPage
  );

  //     
  useEffect(() => {
    setCurrentPage(0);
  }, [activeTab]);

  //  
  const goToPrevPage = () => {
    if (currentPage > 0) {
      setCurrentPage(currentPage - 1);
    }
  };

  const goToNextPage = () => {
    if (currentPage < totalPages - 1) {
      setCurrentPage(currentPage + 1);
    }
  };

  return (
    <section className={`${styles.gallery} ${className || ''}`}>
      <div className={styles.container}>
        {/*   */}
        <div className={styles.header}>
          <h2 className={styles.sectionTitle}></h2>
          <p className={styles.sectionDescription}>
               
          </p>
        </div>

        {/*   */}
        <div className={styles.tabs}>
          <button
            className={`${styles.tab} ${activeTab === 'photos' ? styles.active : ''}`}
            onClick={() => setActiveTab('photos')}
          >
             ({photos.length})
          </button>
          <button
            className={`${styles.tab} ${activeTab === 'music' ? styles.active : ''}`}
            onClick={() => setActiveTab('music')}
          >
             ({music.length})
          </button>
          <button
            className={`${styles.tab} ${activeTab === 'videos' ? styles.active : ''}`}
            onClick={() => setActiveTab('videos')}
          >
             ({videos.length})
          </button>
        </div>

        {/*   -   */}
        <div className={styles.galleryWrapper}>
          {/*   */}
          {totalPages > 1 && (
            <button
              className={`${styles.arrowButton} ${styles.prevArrow}`}
              onClick={goToPrevPage}
              disabled={currentPage === 0}
              aria-label=" "
            >
              
            </button>
          )}

          {/*  */}
          <div className={styles.grid}>
            {paginatedItems.length > 0 ? (
              paginatedItems.map((item, index) =>
                renderMediaItem(item, currentPage * itemsPerPage + index)
              )
            ) : (
              <div className={styles.emptyState}>
                <p>   .</p>
              </div>
            )}
          </div>

          {/*   */}
          {totalPages > 1 && (
            <button
              className={`${styles.arrowButton} ${styles.nextArrow}`}
              onClick={goToNextPage}
              disabled={currentPage === totalPages - 1}
              aria-label=" "
            >
              
            </button>
          )}
        </div>

        {/*   */}
        {totalPages > 1 && (
          <div className={styles.pagination}>
            {(() => {
              const isMobile = window.innerWidth <= 768;
              const maxVisible = isMobile ? 5 : 10;
              let startPage = Math.max(0, currentPage - Math.floor(maxVisible / 2));
              const endPage = Math.min(totalPages - 1, startPage + maxVisible - 1);

              if (endPage - startPage < maxVisible - 1) {
                startPage = Math.max(0, endPage - maxVisible + 1);
              }

              const pages = [];

              // :    ( )
              if (!isMobile && currentPage > 0) {
                pages.push(
                  <button
                    key="first"
                    className={styles.pageButton}
                    onClick={() => setCurrentPage(0)}
                    aria-label=" "
                  >
                    
                  </button>
                );
              }

              // : 5   () -  
              if (isMobile) {
                pages.push(
                  <button
                    key="jump-prev"
                    className={styles.pageButton}
                    onClick={() => setCurrentPage(Math.max(0, currentPage - 5))}
                    disabled={currentPage === 0}
                    aria-label="5 "
                  >
                    
                  </button>
                );
              }

              //  
              pages.push(
                <button
                  key="prev"
                  className={styles.pageButton}
                  onClick={goToPrevPage}
                  disabled={currentPage === 0}
                  aria-label=""
                >
                  
                </button>
              );

              //  
              for (let i = startPage; i <= endPage; i++) {
                pages.push(
                  <button
                    key={i}
                    className={`${styles.pageButton} ${currentPage === i ? styles.active : ''}`}
                    onClick={() => setCurrentPage(i)}
                    aria-label={` ${i + 1}`}
                  >
                    {i + 1}
                  </button>
                );
              }

              //  
              pages.push(
                <button
                  key="next"
                  className={styles.pageButton}
                  onClick={goToNextPage}
                  disabled={currentPage === totalPages - 1}
                  aria-label=""
                >
                  
                </button>
              );

              // : 5   () -  
              if (isMobile) {
                pages.push(
                  <button
                    key="jump-next"
                    className={styles.pageButton}
                    onClick={() => setCurrentPage(Math.min(totalPages - 1, currentPage + 5))}
                    disabled={currentPage === totalPages - 1}
                    aria-label="5 "
                  >
                    
                  </button>
                );
              }

              // :    ( )
              if (!isMobile && currentPage < totalPages - 1) {
                pages.push(
                  <button
                    key="last"
                    className={styles.pageButton}
                    onClick={() => setCurrentPage(totalPages - 1)}
                    aria-label=" "
                  >
                    
                  </button>
                );
              }

              return pages;
            })()}
          </div>
        )}
      </div>

      {/*  (   ) */}
      {selectedItem && (
        <div className={styles.lightbox} onClick={closeLightbox}>
          <button className={styles.closeButton} aria-label="">
            
          </button>

          {/*   ( ) */}
          <button
            className={`${styles.navButton} ${styles.prevButton}`}
            onClick={(e) => {
              e.stopPropagation();
              navigatePrevious();
            }}
            aria-label=""
          >
            
          </button>

          {/*   ( ) */}
          <button
            className={`${styles.navButton} ${styles.nextButton}`}
            onClick={(e) => {
              e.stopPropagation();
              navigateNext();
            }}
            aria-label=""
          >
            
          </button>

          <div
            className={styles.lightboxContent}
            onClick={(e) => e.stopPropagation()}
            onTouchStart={onTouchStart}
            onTouchMove={onTouchMove}
            onTouchEnd={onTouchEnd}
          >
            {selectedItem.type === 'photo' && (
              <div className={styles.imageWrapper}>
                <img src={selectedItem.url} alt={selectedItem.title} />
                {/*    */}
                <div
                  className={styles.clickAreaLeft}
                  onClick={(e) => {
                    e.stopPropagation();
                    navigatePrevious();
                  }}
                  aria-label=" "
                />
                <div
                  className={styles.clickAreaRight}
                  onClick={(e) => {
                    e.stopPropagation();
                    navigateNext();
                  }}
                  aria-label=" "
                />
              </div>
            )}
            {selectedItem.type === 'music' && (
              <audio controls src={selectedItem.url} autoPlay />
            )}
            {selectedItem.type === 'video' && (
              <video controls src={selectedItem.url} autoPlay />
            )}
            <div className={styles.lightboxInfo}>
              <h3>{selectedItem.title}</h3>
              {selectedItem.description && <p>{selectedItem.description}</p>}
              <p className={styles.lightboxCounter}>
                {selectedIndex + 1} / {getCurrentItems().length}
              </p>
            </div>
          </div>
        </div>
      )}
    </section>
  );
};

export default MediaGallery;